import React, { Fragment } from 'react';
import styled from '@emotion/styled';
import fondo from '../fondo.jpg';

const Header = () => {

    //Styled Components
    const ContenedorHeader = styled.div`
        width: 100%;
        height: 90vh;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        background-image: linear-gradient(rgba(14, 94, 94, 0.174), rgba(185, 185, 185, 0.174)), url("${fondo}");
        background-position: center;
        background-size: cover;
        background-attachment: fixed;
    `;

    const TextoPrincipal = styled.h1`
        font-size: 60px;
        color: #ffff;
        padding-bottom: 15px;
        text-align: center;
        font-weight: 600;
    `;

    const Slogan = styled.p`
        text-align: center;
        color: #ffff;
        font-size: 20px;
    `;

    const Hamburguesa = styled.div`
        position: fixed;
        width: auto;
        height: auto;
        top: 1px;
        right: 5px;
        cursor: pointer;
    `;

    return ( 
        <Fragment>
            <Hamburguesa>
            <svg xmlns="http://www.w3.org/2000/svg" className="icon icon-tabler icon-tabler-menu-2" width="44" height="44" viewBox="0 0 24 24" strokeWidth="1.5" stroke="#000000" fill="none" strokeLinecap="round" strokeLinejoin="round">
                <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                <line x1="4" y1="6" x2="20" y2="6" />
                <line x1="4" y1="12" x2="20" y2="12" />
                <line x1="4" y1="18" x2="20" y2="18" />
            </svg>
            </Hamburguesa>
            <ContenedorHeader>
                <div>
                    <TextoPrincipal>Ground Irrigaciones</TextoPrincipal>
                    <Slogan>Expertos en sistemas de riego automático</Slogan>
                </div>
            </ContenedorHeader>
        </Fragment>
     );
}
 
export default Header;