import React, { Fragment } from 'react';

const Servicios = () => {
    return ( 
        <Fragment>
            <div className="servicios">
                <h2 className="titulo-servicios titulo">Servicios</h2>
                <div className="contenedores-servicios">
                    <div className="contenedor-servicio design">
                        <h3>Diseño</h3>
                        <div className="hover-servicio">
                            <p>Eficiente, tomando en consideración los requerimientos y necesidades de cada tipo de vegetación.</p>
                        </div>
                    </div>
                    <div className="contenedor-servicio instalacion">
                        <h3>Instalación</h3>
                        <div className="hover-servicio">
                            <p>Alineada con las normas oficiales de la ley de aguas nacionales (SEMARNAT), con personal y materiales de primera calidad.</p>
                        </div>
                    </div>
                    <div className="contenedor-servicio mantenimiento">
                        <h3>Mantenimiento</h3>
                        <div className="hover-servicio">
                            <p>Revisión, cambio y limpieza de equipos de sistemas de riego automático.</p>
                        </div>
                    </div>
                    <div className="contenedor-servicio bombeo">
                        <h3>Bombeo</h3>
                        <div className="hover-servicio">
                            <p>Instalación de bombas para aguas residuales, comerciales y agrícolas.</p>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
     );
}
 
export default Servicios;